        "policy__subtitle": "18 Dcembre 2019",
        "policy__text": "Salut!\nNous collectons des informations sur nos utilisateurs. Voici quelques exemples (mais pas tous):\n Langue. Pour savoir quelle langue utiliser pour rester en contact avec vous.\n Email. Pour savoir comment rester en contact avec vous.\n Heure  laquelle vous avez envoy un e-mail.\n Les rsultats du sondage social. Analyser notre public.\n Version d'atterrissage. Comprendre le contexte.\nNous utilisons vos donnes uniquement pour dvelopper notre produit.\nNous pouvons modifier cette politique de confidentialit quand nous le voulons. Si vous utilisez les produits de Reface (y compris le site, les applications, etc.), vous acceptez cette politique de confidentialit.",
