        "reface__actions__description": "La liste des actions disponibles sera largie. Actuellement, il existe une intgration avec IFTTT via Webhook, ainsi que la possibilit d'ajouter des actions par des dveloppeurs tiers via API (par exemple, \"cliquez sur un bouton\").",
