        "reface__computer_connection__title": "Connexion ordinateur",
        "reface__diagnostics__description": "Vous pouvez afficher des informations sur la prcision des lectrodes, ainsi que le dernier geste reconnu.",
        "reface__diagnostics__title": "A propos de l'appareil",
        "reface__retraining__description": "Si vos gestes sont mal interprts par le classificateur, vous pouvez les rapprendre.\nDeux modes de recyclage sont disponibles: manuel et automatique.\nEn mode manuel, vous allez enregistrer de courts segments de vido et indiquer o le geste a t fait. L'ordinateur le fera automatiquement pour vous.",
