        "reface__computer_connection__title": "Collegamento al computer",
        "reface__diagnostics__description": " possibile visualizzare le informazioni circa l'esattezza degli elettrodi, cos come l'ultimo gesto riconosciuto.",
        "reface__diagnostics__title": "A proposito di dispositivo",
        "reface__retraining__description": "Se i vostri gesti sono erroneamente interpretate dal classificatore, si pu imparare di nuovo.\nSono disponibili due modalit di riqualificazione disponibili: manuale e automatico.\nIn modalit manuale, si registra brevi segmenti di video e di indicare dove  stato fatto il gesto. Il computer verr automaticamente fare questo per voi.",
